
@import "./stylesheets/global.scss";

.App
{
	width: 100vw;
	height: 100vh;
	
	background-color: #e1bee7;
}

.Heart
{
	position: relative;
	width: 15vw;
	height: 15vw;
	
	
	left: 42.5vw;
	
	top: 40%;
	
	background-color: red;
	
	transform: perspective(1000px) rotateZ(-45deg);
	transform-style: preserve-3d;
	
	animation: Heart-Animation 1s infinite linear;
	
	box-shadow: 20px -5px 50px rgba(0,0,0,0.3);
}

@keyframes Heart-Animation
{
	0%
	{
		transform: perspective(1000px) rotateY(0deg) rotateZ(-45deg);
	}
	
	100%
	{
		transform: perspective(1000px) rotateY(180deg) rotateZ(-45deg);
	}
}

.HeartLeft,
.HeartRight
{
  background-color: red;
  border-radius: 50%;
  height: 15vw;
  position: absolute;
  width: 15vw;
  
}

.HeartLeft
{
	top: -7.5vw;
	left: 0px;
}

.HeartRight
{
	top: 0px;
	left: 7.5vw;
}


.BottomText
{
	display: block;
	position: absolute;
	
	width: 100vw;
	
	text-align: center;
	font-size: 300%;
	font-weight: 500;
	
	text-shadow: 2px 2px #999999;
	
	bottom: 20vh;
}
